@import url("https://fonts.googleapis.com/css?family=Roboto:400,700&display=swap");
/* --- CSS VARIABLES --- */
/* https://developer.mozilla.org/en-US/docs/Web/CSS/var */
:root {
  --text-color: black;
  /* #2a8c47; */
  --green-color: grey;
}

/* GENERAL STYLES */

body {
  font-family: "Roboto", "Verdana", sans-serif;
  font-weight: 400;
  line-height: normal;
}

a {
  text-decoration: none;
  color: var(--text-color);
}

a:hover {
  color: var(--green-color);
}

h1 {
  font-size: 70px;
  margin-bottom: 20px;
}

h2 {
  color: var(--text-color);
  font-size: 8px;
  display: inline;
  text-transform: uppercase;
  letter-spacing: 6px;
}

h3 {
  color: var(--text-color);
  font-size: 26px;
}
h4 {
  color: var(--text-color);
  font-size: 15px;
}
th,
td {
  padding: 5px;
  text-align: left;
}

.sessionicon {
  width: 20px;
  height: auto;
}

p {
  color: var(--text-color);
  font-size: 13px;
}
/* END GENERAL STYLES */

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Roboto", sans-serif;
  color: black;
}

.container {
  /* display: flex;
  justify-content: center; */
}
/* /// PAGE 404 // */
.mainbox {
  background-color: #333;
  margin: auto;
  height: 600px;
  width: 600px;
  position: relative;
}

.err {
  color: #ffffff;
  font-family: "Nunito Sans", sans-serif;
  font-size: 11rem;
  position: absolute;
  left: 20%;
  top: 8%;
}

.far {
  position: absolute;
  font-size: 8.5rem;
  left: 42%;
  top: 15%;
  color: #ffffff;
}

.err2 {
  color: #ffffff;
  font-family: "Nunito Sans", sans-serif;
  font-size: 11rem;
  position: absolute;
  left: 68%;
  top: 8%;
}

.err3 {
  color: #ffffff;
  font-family: "Nunito Sans", sans-serif;
  font-size: 11rem;
  position: absolute;
  left: 44%;
  top: 8%;
}

.msg {
  text-align: center;
  font-family: "Roboto", sans-serif;
  font-size: 1.6rem;
  position: absolute;
  left: 16%;
  top: 45%;
  width: 75%;
  color: white;
}
.msg a {
  text-decoration: none;
  color: white;
}
.msg a:hover {
  text-decoration: none;
  color: grey;
}
/* /// END OF PAGE 404 // */
